@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200&Inconsolata:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html{padding:env(safe-area-inset)}body{-webkit-align-items:center;align-items:center;background-color:#f6e0e0;box-sizing:border-box;color:#000;display:-webkit-flex;display:flex;font-family:Raleway,sans-serif;font-weight:bold;height:100vh;-webkit-justify-content:center;justify-content:center;width:100%}button{background-color:inherit;border:none;color:#000;font-size:1.5em;opacity:0.7}button:focus{opacity:0.3;outline:none}#app{-webkit-align-items:center;align-items:center;border-radius:1rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin:0.2rem 0;padding-bottom:0.5rem}#app #length-control{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}#app #length-control #session-wrapper,#app #length-control #short-wrapper,#app #length-control #fourth-wrapper{display:inline-block;margin:0em;padding:0;text-align:center}#app #length-control #session-wrapper p,#app #length-control #short-wrapper p,#app #length-control #fourth-wrapper p{margin:0.1rem}#app #length-control #session-wrapper input,#app #length-control #short-wrapper input,#app #length-control #fourth-wrapper input{background-color:inherit;border:none;display:inline-block;font-family:Inconsolata,monospace;font-size:1.3em;max-width:7rem;padding:0;text-align:center}#app #length-control p{text-align:center}#app #timer-controls-wrapper{-webkit-align-items:center;align-items:center;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center}#app #timer-controls-wrapper #timer{display:block;height:15rem;margin:1em 0;position:relative;text-align:center;width:16rem}#app #timer-controls-wrapper #timer #timer-svg{-webkit-transform:scaleX(-1);transform:scaleX(-1)}#app #timer-controls-wrapper #timer #timer-svg #timer-circle{fill:none;stroke:none}#app #timer-controls-wrapper #timer #timer-svg #timer-circle #timer-circle-outter,#app #timer-controls-wrapper #timer #timer-svg #timer-circle #timer-circle-inner{stroke:rgba(255,255,255,0.5);stroke-width:0.3rem}#app #timer-controls-wrapper #timer #timer-svg #timer-circle #timer-circle-inner-rem,#app #timer-controls-wrapper #timer #timer-svg #timer-circle #timer-circle-outer-rem{stroke-linecap:round;stroke-width:0.3rem;-webkit-transform:rotate(90deg);transform:rotate(90deg);-webkit-transform-origin:center;transform-origin:center;transition:1s linear all}#app #timer-controls-wrapper #timer #timer-text{left:50%;position:absolute;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}#app #timer-controls-wrapper #timer #timer-text #timer-label{font-size:1.5em}#app #timer-controls-wrapper #timer #timer-text #time-left{font-family:Inconsolata,monospace;font-size:3em;margin-top:0}@media screen and (min-width: 560px){#app{-webkit-flex-direction:row;flex-direction:row}}

